import React from 'react';

import './About.css'

function About(props) {
	return (
		<div className='about'>
			<p>Zephyr Media is an up and coming media company with its roots in aerial photography videography for real estate, using the most current UAVs for filming and software for editing.</p>
		</div>
	)
}

export default About


